 html, body {
     margin: 0;
     padding: 0;
}
 body {
     font-family: 'Roboto', sans-serif;
     background:  #DFDFDF;
}
 $color-main2: #FF3115;
 $color-main1: #888888;
 $color-main3: #202020;
 %space-between {
     display: flex;
     justify-content: space-between;
}
 %center {
     display: flex;
     justify-content: center;
}
 .center {
     width: 100%;
     @extend %center;
}
 i {
     color: $color-main2;
}
 .width60 {
     width: 60%;
     @extend %space-between;
}
 .dark-line {
     background: $color-main3;
     width: 100%;
     height: 35px;
     @extend %center;
     @extend %space-between;
     .welcome {
         font-size: 13px;
         color: white;
         margin-top: 10px;
         p {
             display: inline;
        }
    }
     .networks {
         color: $color-main1;
         margin: 10px 0 0 0;
    }
}
 .white-line {
     width: 100%;
     @extend %center;
     @media screen and (max-width: 920px) {
         .contact {
             display: none;
        }
    }
     .logo {
         margin: 20px 0 0 0;
         i {
             font-size: 40px;
        }
         p {
             font-size: 40px;
             font-weight: 1000;
             display: inline;
        }
    }
     .contact {
         @extend %space-between;
         width: 660px;
         .telegram i{
             color: #34ABE3 !important;
        }
         .Us {
             margin: 20px 0 0 0;
             span {
                 font-weight: 1000;
                 font-size: 19px;
            }
             p {
                 position: relative;
                 left: 38px;
            }
             i {
                 font-size: 35px;
                 display: inline;
            }
        }
    }
}
 .menu {
     width: 100%;
     @extend %center;
     height: 60px;
     margin-top: 20px;
     .menu-phone {
         width: 100%;
         display: none;
         margin: 0;
         ul {
             list-style: none;
             padding: 0;
             margin: 0;
             li {
                 width: 100%;
                 height: 30px;
                 background: $color-main3;
                 margin-bottom: 10px;
                 list-style-type: none;
                 @extend %center;
                 align-items: center;
                 a {
                     text-decoration: none;
                     color: #fff;
                     transition: color .3s ease-out;
                     span {
                         color: $color-main2;
                    }
                }
                 a:hover {
                     color: $color-main2;
                }
            }
        }
         @media screen and (max-width: 920px) {
             display: block;
        }
    }
     .menu-container {
         width: 60%;
         background: $color-main3;
         display: flex;
         justify-content: space-around;
         align-items: center;
         @media screen and (max-width: 920px) {
             display: none;
        }
         a {
             text-decoration: none;
             color: #989898;
             font-size: 22px;
             @media screen and (max-width: 1360px) {
                 font-size: 16px;
            }
             span {
                 color: $color-main2;
            }
        }
         a:hover {
             color: $color-main2;
        }
    }
}
 .hero-text {
     width: 100%;
     margin-top: 120px;
     background: #969696;
     @extend %center;
     h1 {
         text-align: center;
         font-size: 42px;
         color: #fff;
         @media screen and (max-width: 690px) {
             font-size: 24px;
        }
    }
}
 figure {
     display: block;
     position: absolute 
}
 .triangle {
     border-color: transparent transparent rgba(111, 111, 111, 0.8);
     border-width: 0 125px 216px;
     border-style: solid 
}
 .tetrahedron {
     position: relative;
     right: 20px;
     margin: 20px auto;
     width: 300px;
     height: 300px;
     -webkit-transform-style: preserve-3d;
     -moz-transform-style: preserve-3d;
     transform-style: preserve-3d;
     -webkit-animation: initialrotate infinite 3s;
     -moz-animation: initialrotate infinite 3s;
     animation: initialrotate infinite 3s;
}
 .tetrahedron:hover {
     opacity: 0.6;
     -webkit-transform-style: preserve-3d;
     -moz-transform-style: preserve-3d;
     transform-style: preserve-3d;
     -webkit-animation: hoverrotate linear infinite 14s;
     -moz-animation: hoverrotate linear infinite 14s;
}
 .tetrahedron figure {
     height: 216px 
}
 .tetrahedron .front {
     -moz-transform: rotatex(19.5deg);
     -ms-transform: rotatex(19.5deg);
     -o-transform: rotatex(19.5deg);
     -webkit-transform: rotatex(19.5deg);
     transform: rotatex(19.5deg);
     -moz-transform-origin: 50% 0 0;
     -ms-transform-origin: 50% 0 0;
     -o-transform-origin: 50% 0 0;
     -webkit-transform-origin: 50% 0 0;
     transform-origin: 50% 0 0 
}
 .tetrahedron .bottom {
     -moz-transform: rotatex(90deg) translatey(-35px) translatez(-96px);
     -ms-transform: rotatex(90deg) translatey(-35px) translatez(-96px);
     -o-transform: rotatex(90deg) translatey(-35px) translatez(-96px);
     -webkit-transform: rotatex(90deg) translatey(-35px) translatez(-96px);
     transform: rotatex(90deg) translatey(-35px) translatez(-96px);
     -moz-transform-origin: 0% 100% 0%;
     -ms-transform-origin: 0% 100% 0%;
     -o-transform-origin: 0% 100% 0%;
     -webkit-transform-origin: 0% 100% 0%;
     transform-origin: 0% 100% 0% 
}
 .tetrahedron .left {
     -moz-transform: translatex(0) rotatey(60deg) rotatex(-19.5deg);
     -ms-transform: translatex(0) rotatey(60deg) rotatex(-19.5deg);
     -o-transform: translatex(0) rotatey(60deg) rotatex(-19.5deg);
     -webkit-transform: translatex(0) rotatey(60deg) rotatex(-19.5deg);
     transform: translatex(0) rotatey(60deg) rotatex(-19.5deg);
     -moz-transform-origin: 50% 0 0;
     -ms-transform-origin: 50% 0 0;
     -o-transform-origin: 50% 0 0;
     -webkit-transform-origin: 50% 0 0;
     transform-origin: 50% 0 0 
}
 .tetrahedron .right {
     -moz-transform: translatex(0) rotatey(-60deg) rotatex(-19.5deg);
     -ms-transform: translatex(0) rotatey(-60deg) rotatex(-19.5deg);
     -o-transform: translatex(0) rotatey(-60deg) rotatex(-19.5deg);
     -webkit-transform: translatex(0) rotatey(-60deg) rotatex(-19.5deg);
     transform: translatex(0) rotatey(-60deg) rotatex(-19.5deg);
     -moz-transform-origin: 50% 0 0;
     -ms-transform-origin: 50% 0 0;
     -o-transform-origin: 50% 0 0;
     -webkit-transform-origin: 50% 0 0;
     transform-origin: 50% 0 0 
}
 @-webkit-keyframes initialrotate {
     to {
         -webkit-transform: rotatez(360deg) rotatex(360deg);
         transform: rotatez(360deg) rotatex(360deg) 
    }
}
 @-moz-keyframes initialrotate {
     to {
         -moz-transform: rotatez(360deg) rotatex(360deg);
         transform: rotatez(360deg) rotatex(360deg) 
    }
}
 @keyframes initialrotate {
     to {
         transform: rotateZ(360deg) rotateX(360deg) 
    }
}
 @-webkit-keyframes hoverrotate {
     to {
         -webkit-transform: rotatex(360deg) rotatey(360deg) rotatez(360deg);
         transform: rotatex(360deg) rotatey(360deg) rotatez(360deg) 
    }
}
 @-moz-keyframes hoverrotate {
     to {
         -moz-transform: rotatex(360deg) rotatey(360deg) rotatez(360deg);
         transform: rotatex(360deg) rotatey(360deg) rotatez(360deg) 
    }
}
 @keyframes hoverrotate {
     to {
         transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg) 
    }
}
 .button {
     background: #969696;
     margin-top: 100px;
     width: 100%;
     @extend %center;
     transition: .3s all ease-in-out;
     a {
         text-decoration: none;
         color: #fff;
         font-size: 24px;
         transition: .5s all ease-out;
    }
}
 .button:hover {
     background: #FF1542;
}


