%center {
     display: flex;
     justify-content: center;
}
 #particles-js {
     position: fixed;
     z-index: -1;
     width: 100vw;
     height: 100vh;
}
 .main-text {
     width: 100%;
     margin-top: 40px;
     h2 {
         text-align: center;
         padding: 80px 0 40px 0;
         border-bottom: 2px solid red;
         font-size: 36px;
    }
}
 .cources {
     width: 60%;
     display: grid;
     grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
     grid-gap: 2vh;
     place-content: center center;
}
 .cource {
     min-height: 400px;
     margin-top: 40px;
     border: 1px solid #111;
     @extend %center;
     align-items: center;
     background: #212121;
     transition: all .5s ease-in-out;
     .content {
         width: 100%;
         h3 {
             text-align: center;
             font-size: 22px;
             color: #FF3115;
             margin-bottom: 30px;
        }
         p {
             text-align: center;
             color: #fff;
        }
         .button {
             @extend %center;
             align-items: center;
             background: #969696;
             height: 40px;
             margin-top: 40px;
             &:hover {
                background: #FF1542;
             }
             a {
                 text-decoration: none;
                 color: #fff;
                 opacity: 0.7;
                 &:hover {
                     opacity: 1;
                }
            }
        }
    }
}
